* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: $clr6;
}

html {
    font-family: "montserrat", sans-serif;
    line-height: 1.6;
    color: $clr2;
    scroll-behavior: smooth;
}

body {
    margin: 0;
}

img {
    width: 100%;
}

